export default {
	onLoad(option) {
		this.title = option.title;
		if(option.title =="好剧预告"){
			this.morelist = this.$store.state.demolist2;
		}
		else{
			this.morelist = this.$store.state.demolist;
		}
	},
	data() {
		return {
			morelist:"",
			title:"",
		}
	},
	methods: {
		details(index){
			uni.navigateTo({
				url:"../../pages/details/details"+"?id="+index+"&name="+this.title,
			})
		}
	}
}
